<template>
<div>
    <div class="client-info-detail frame-container box">
         <!-- 补白 -->
        <div class="client-info-detail__padding"></div>
        <!-- 导航栏 -->
        <div style="position: relative;z-index: 999" class="client-info-detail__nav box plr-30 align-hor-bet">
          <div class="icon iconfont  iconreturn"
            @click="onBack">
          </div>
          <div class="title">微信</div>
          <div
            class="text"
            style="background-color:#4ba8ff;
                padding:3px;
                color:white;
                border-radius:5px;"
            @click="saveBtn"
          >保存</div>
        </div>
        <div class="client-info-detail__content">
          <div class="match-width box align-default">
              <div class="title">经营者信息</div>
              <mu-form>
                  <mu-form-item style="margin:0 10px;padding:0" label prop="businessLevOne" :rules="businessLevOneRules">
                  <FzyCascaderTree
                      v-model="cooperationLevArr"
                      @change1="change1"
                      :dataTree="cooperationLevTree"
                      :label="'经营类别'"
                      :placeholder="'请选择经营类别'"
                      :modalLabel="'选择经营类别'"
                      :required="true"
                  ></FzyCascaderTree>
                  <mu-text-field v-model="params.businessLevOne" style="display: none"></mu-text-field>
                  </mu-form-item>
              </mu-form>
              <div class="item">
                <div class="subtitle">
                    <span class="star"></span>特殊资质
                </div>
                <div class="match-left-space align-right">
                    <div class="match-left-space" @click="callTakePhoto(1)">&nbsp;</div>
                    <div
                    class="input align-right"
                    style="width: auto">
                    <img
                        v-if="detail.specialQualificationPhotoId"
                        :src="detail.specialQualificationPhotoId | loadImage"
                        @click="previewImage(detail.specialQualificationPhotoId)">
                    <!-- <div v-else class="img"></div> -->
                    <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(1)"></div>
                    </div>
                </div>
            </div>
            <div class="item" style="">
                <div class="subtitle">
                    <span class="star"></span>补充材料
                </div>
                <div class="match-left-space align-right">
                    <div class="match-left-space" @click="callTakePhoto(2)">&nbsp;</div>
                    <div
                    class="input align-right"
                    style="width: auto">
                    <img
                        v-if="detail.businessAdditionPics"
                        :src="detail.businessAdditionPics | loadImage"
                        @click="previewImage(detail.businessAdditionPics)">
                    <!-- <div v-else class="img"></div> -->
                    <div class="icon iconfont iconenter ml-10" @click="callTakePhoto(2)"></div>
                    </div>
                </div>
            </div>
            <div class="match-width box align-center">
                <div
                class="next box align-center"
                @click="submitBtn">
                    确认提交
                </div>
            </div>
          </div>
        </div>
    </div>
   
  <!-- 隐藏获取图片 -->
  <div class="file">
      <input type="file" ref="fileCamera" capture="camera" accept="image/*" @change="onFileChange"/>
      <div class="cover"></div>
  </div>
  <!-- 隐藏获取图片 -->
  <div class="file">
      <input type="file" ref="file" accept="image/*" @change="onFileChange"/>
      <div class="cover"></div>
  </div>
  <!-- 图片预览 -->
  <div class="cover-x align-default" v-if="isImagePreview" @click="isImagePreview = false">
      <div class="match-left-space"></div>
      <div class="image-preview">
          <img class="match-parent" :src="imgUrl | previewLoadImage">
      </div>
      <div class="match-left-space"></div>
  </div>
  <!-- action-sheet -->
  <mu-bottom-sheet :open.sync="openMenu">
  <div class="action-sheet box align-default menu">
      <div
      class="item align-center"
      @click="onTakePhoto(1)">
      拍照
      </div>

      <div
      class="item align-center"
      @click="onTakePhoto(2)">
      从手机相册选择
      </div>

      <div
      class="item align-center cancel"
      @click="onActionMenuShow(2)">
      取消
      </div>

      <!-- 补脚 -->
      <div class="action-sheet__padding"></div>

  </div>
  </mu-bottom-sheet>
</div>
</template>

<script>
import { url } from '@/utils/src/request'
import { CLIENT_INFO_DETAIL } from '@/router/types'
import { clientInfoApi } from '@/api'
import { afterLoginInfoLocal, clientInfoDetailIDLocal, clientInfoDetailLocal, clientInfoDetailMerchantNameLocal } from '@/storage'
import FzyCascaderTree from "@/components/common/fzyCascaderTree";
import { getCateGory } from '@/api/businessCategory'
import typeJson from "@/assets/merchant/merchantType.json";
import { agentOrClient } from "@/api";
export default {
  components: { FzyCascaderTree },
  data() {
    return {
      isImagePreview: false,
      openMenu: false,
      detail: {},
      params: {
        companyId: afterLoginInfoLocal.getJSON().companyId,
        managerId: afterLoginInfoLocal.getJSON().userId,
        status: "1" // 正常状态
      },
      businessLevOneRules: [{ validate: val => !!val, message: "" }],
      cascaderArr: [],
      cascaderTree: [],
      cooperationLevArr: [],
      cooperationLevTree: [],
      
    };
  },
  watch: {
    cooperationLevArr(val, oldVal) {
      val = val || [];
      this.params.businessLevOne = val[0] || "";
      this.params.businessLevTwo = val[1] || "";
      this.params.businessLevThree = val[2] || "";
    },
    cascaderArr(val, oldVal) {
      val = val || [];
      this.params.province = val[0] || "";
      this.params.city = val[1] || "";
      this.params.area = val[2] || "";
    }
  },
   filters: {
      // 获取图片
      loadImage (id) {
      return id ? (url + `/fms/upload/resource/thumbnail/${id}`) : ''
      },
      previewLoadImage (id) {
      return id ? url + `/fms/upload/resource/${id}` : ''
      }
  },
  created() {
    this.intiMerchantType();
    this.id = JSON.parse(clientInfoDetailIDLocal.get())
    if (this.id) {
      this.initDetail()
      this.sourceData = getCateGory()
      this.tmpSourceData = this.sourceData
    } else {
      this.$toast.error('详情数据丢失')
    }
  },
  mounted() {},
  methods: {
    //保存按钮
    saveBtn() {
        clientInfoApi.merchantAdd(this.detail).then( res => {
            console.log(res)
            if( res.code === 200 ) {
            this.$toast.success(res.msg);
            }else {
            this.$toast.info(res.msg);
            }
        }, (err) => {
            this.$toast.success(err.msg);
        })
    },
    intiMerchantType() {
      let newArr = this.loopSet(typeJson);
      this.cooperationLevTree = newArr;
    },
    loopSet(arr) {
      arr.forEach(item => {
        this.$set(item, "id", item.name);
        if (item.children && item.children.length) {
          item.children = this.loopSet(item.children);
        }
        return item;
      });
      return arr;
    },
    // 返回
    onBack () {
        this.$router.push({
            name: CLIENT_INFO_DETAIL
        })
    },
    //提交
    submitBtn() {
        let that = this;
        console.log(111)
        var detail = {
            id: that.detail.id,
            specialQualificationPhotoId: that.detail.specialQualificationPhotoId,
            businessAdditionPics: that.detail.businessAdditionPics,
            settlementId: that.detail.settlementId,
            qualificationType: that.detail.qualificationType
        };
        clientInfoApi.wxInto(detail).then(res => {
            if( res.code === 200 ) {
                this.$toast.success( '提交成功！请留意进件状态' );
                this.$router.push({
                    name: CLIENT_INFO_DETAIL
                })
            }
            console.log( res)
        })
        console.log(detail)
    },
    // 初始化detail对象
    initDetail () {
      this.detail = JSON.parse(clientInfoDetailLocal.get())
      this.detail.subjectType == 'SUBJECT_TYPE_INDIVIDUAL' ? this.comTypeName = '个体工商户' : this.detail.subjectType == 'SUBJECT_TYPE_ENTERPRISE' ? this.comTypeName = '企业' : '商户类型小微';
      this.merchantName = JSON.parse(clientInfoDetailMerchantNameLocal.get())
      this.merchantName = this.merchantName.merchantName
    },
    // 预览图片
    previewImage (id) {
    // console.log(id)
        if (id) {
            this.imgUrl = id
            this.isImagePreview = true
        }
    },
    // 获取图片
    callTakePhoto (index) {
        // index
        // 1 特殊资质
        this.photoTaker = +index
        this.openMenu = true
    },
    // 文件改变
    onFileChange (e) {
        if (!e.target.files) {
            return
        }
        let file = e.target.files[0]
        // let file = this.$refs.file.files[0]
        if (file) {
            // console.log(e)
            clientInfoApi.uploadImage(file).then(res => {
                if (res.code === 200) {
                    this.$toast.success('图片上传成功')
                    let photoId = res.obj
                    // console.log(photoId)
                    // 1 app
                    // 2 公众号
                    // 3 特殊资质
                    // 4 补充材料
                    if (this.photoTaker === 1) {
                        this.$set(this.detail, 'specialQualificationPhotoId', photoId)
                    }else if(this.photoTaker === 2) {
                        this.$set(this.detail, 'businessAdditionPics', photoId)
                    } else {
                        this.$toast.error(res.msg)
                    }
                }
            })
        }
    },
    // 相机打开
    onTakePhoto (status) {
        if (+status === 1) {
            this.$refs.fileCamera.click()
            // 拍照
        } else if (+status === 2) {
            this.$refs.file.click()
            // 从相机打开
        }
        this.openMenu = false
    },
    // 获取图片
    getImage (id) {
        return id ? url + `/fms/upload/resource/thumbnail/${id}` : ''
    },
    change1(e) {
        this.detail.qualificationType = e[1].name
        this.detail.settlementId = e[1].value
    },
    intiMerchantType() {
        let newArr = this.loopSet(typeJson);
        this.cooperationLevTree = newArr;
    },
    loopSet(arr) {
        arr.forEach(item => {
            this.$set(item, "id", item.name);
            if (item.children && item.children.length) {
            item.children = this.loopSet(item.children);
            }
            return item;
        });
        return arr;
    },
  }
};
</script>
<style scoped>
</style>



